.select {
  font-family: Arial, Helvetica, sans-serif;
  width: 120px;
  text-align: center;
  height: 30px;
  border: 1px solid #999;
  font-size: 18px;
  color: #050505;
  background-color: #eee;
  border-radius: 5px;

  @media (max-width:375px) {
    width: 80px;
    font-size: 14px;
  }
}